const io = require('../../common/weapp.websocket.io.js')
const mixin = require('../../common/tool.js')
var socket = '';
export default {
	data() {
		return {
			InputBottom: 0,
			dataList: [],
			sayText: '',
			tips: 'aa',
			socket: {},
			id: '',
			title: '阿飞的聊天室'
		};
	},
	onLoad() {
		this.Init()
	},
	onHide(){
		console.log(1)
	  socket.close()	
	},
	methods: {
		Init() {
			socket = io('http://www.yangyongfei.com', {
				// 实际使用中可以在这里传递参数
				query: {
					room: 'fg',
					userId: `client_${Math.random()}`,
				},
				transports: ['websocket']
			});
			socket.on('connect', () => {
				console.log(socket)
				const id = socket.id;
				this.id = id
				console.log('#connect,', id, socket);
				// 监听自身 id 以实现 p2p 通讯
				socket.on(id, msg => {
					console.log('#receive,', msg);
				});
			});

			socket.on('fg', msg => {
				this.dataList.push(msg.data.payload)
				console.log(msg)
			})


			// 接收在线用户信息
			socket.on('online', msg => {
				this.title = `阿飞聊天(${msg.clients.length})`
				console.log('#online,', msg);
			});

			// 系统事件
			socket.on('disconnect', msg => {
				console.log('#disconnect', msg);
			});

			socket.on('disconnecting', () => {
				console.log('#disconnecting');
			});

			socket.on('error', (err) => {
				console.log(err);
			});

		},
		sendMsg() {
			var _this = this
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					console.log(loginRes.authResult);
					// 获取用户信息
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							console.log(infoRes)
							socket.emit('exchange', {
								target: 'fg',
								payload: {
									type: 1,
									id: socket.id,
									time: new Date(),
									userInfo:  infoRes.userInfo,
									msg: _this.sayText
								},
							})
						}
					});
				}
			});
		}, 
		InputFocus(e) {
			console.log(e)
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			console.log(e)
			this.sayText = e.detail.value
			this.InputBottom = 0
		},
		close(){
			socket.close()
		}
	},
}
